$(document).ready(function () {
    $.ajax({
        method: "GET",
        url: "https://emma.pixnet.cc/album/elements?set_id=4555593&user=jiney&format=json",
    })
    .done(function (msg) {
        album_img();
        function album_img() {
            for (let i = 0; i < msg.elements.length; i++) {
                let str = '';
                str += '<div class="col-md-3"><img id="lightbox' + [i] + '" class="img_size w-100 img-fluid" src="' + msg.elements[i].thumb + '" alt=""><p class="pt-2 pb-2 mb-0 text-content text-center">' + msg.elements[i].title + '</p></div>';
                //str += '<a id="lightbox' + [i] + '" href="' + msg.elements[i].thumb + '" data-lightbox="example-set" data-title="Click the right half of the image to move forward."><img class="example-image" src="http://lokeshdhakar.com/projects/lightbox2/images/thumb-3.jpg" alt="" /></a>'
                $('#album_main').append(str);
            };   
        }
    });
});
 